/* Selector Groups: How the CSS selectors are organized for better readability.
 * [Tag Names]: Where HTML tags are modified: they may contain a class equivalent of the tag name.
 * [Device Size Response]: How the element will respond to different device sizes.
 * [Column Size Response]: How the element will respond to different column sizes.
 *------------------
 * CSS Properties: Modifies the visuals of the selector.
 * Display: How the selector will render and interace with other HTML elements.
 * Size: Calculation properties that dictate the HTML box model size (Examples: width, height. padding. margin. border-width).
 * Content: Affects internal inline type content is rendered (Examples: font-family, font-weight, vertical-align).
 * Aesthetic: Non essential, strict visual appeal only (Examples: box-shadow, color).
 
 * Classes: [.header, .subheader, .anchor, .selection]

 * Color Theme
 *------------------
 * Font:			#000000
 * Background:		#FFFFFF
 * Highlight:		#4553FB

 * ==================================================
 * ===================[Basic CSS]====================
 * ==================================================

 * Title: Theme Properties.
 * Description: Adjust global properties. */

// [Tag Names]
body
  // Content
  font-family: sans-serif
  font-size: 14px
  line-height: 1.25em
  // Aesthetic
  color: #000000
  background-color: #FFFFFF

/* Title: Font Properties.
 * Description: Controls how the text will scale. Using only (em's) ensures the font size only needs to be entered once. */

// [Tag Names]
.header, h1, h2, h3, h4, h5, h6
  // Display
  display: block
  // Content
  font-family: inherit

h1
  // Size
  padding: 0 0 0 0
  margin: 1em 0em 0.25em 0em
  // Content
  font-size: 3.75em
  line-height: 0.95em
  &.header
    // Size
    padding: 0 0 0 0
    margin: 1em 0em 0.25em 0em
    // Content
    font-size: 3.75em
    line-height: 0.95em

h2
  // Size
  padding: 0 0 0 0
  margin: 1em 0em 0.25em 0em
  // Content
  font-size: 3em
  line-height: 0.95em
  &.header
    // Size
    padding: 0 0 0 0
    margin: 1em 0em 0.25em 0em
    // Content
    font-size: 3em
    line-height: 0.95em

h3
  // Size
  padding: 0 0 0 0
  margin: 1em 0em 0.25em 0em
  // Content
  font-size: 2.5em
  line-height: 0.95em
  &.header
    // Size
    padding: 0 0 0 0
    margin: 1em 0em 0.25em 0em
    // Content
    font-size: 2.5em
    line-height: 0.95em

h4
  // Size
  padding: 0 0 0 0
  margin: 1em 0em 0.25em 0em
  // Content
  font-size: 2em
  line-height: 0.95em
  &.header
    // Size
    padding: 0 0 0 0
    margin: 1em 0em 0.25em 0em
    // Content
    font-size: 2em
    line-height: 0.95em

h5
  // Size
  padding: 0 0 0 0
  margin: 1em 0em 0.25em 0em
  // Content
  font-size: 1.5em
  line-height: 0.95em
  &.header
    // Size
    padding: 0 0 0 0
    margin: 1em 0em 0.25em 0em
    // Content
    font-size: 1.5em
    line-height: 0.95em

h6
  // Size
  padding: 0 0 0 0
  margin: 1em 0em 0.25em 0em
  // Content
  font-size: 1em
  line-height: 0.95em
  &.header
    // Size
    padding: 0 0 0 0
    margin: 1em 0em 0.25em 0em
    // Content
    font-size: 1em
    line-height: 0.95em

.subheader
  // Display
  display: block
  // Size
  padding: 0 0 0 0
  margin: 1em 0 1.25em 0
  // Aesthetic
  font-size: inherit

h1 + .subheader, h2 + .subheader, h3 + .subheader, h4 + .subheader, h5 + .subheader, h6 + .subheader
  // Size
  padding: 0 0 0 0
  margin: .5em 0 2em 0

hr
  // Size
  display: block
  // Size
  margin: 1.25em 0

h1 + hr, h2 + hr, h3 + hr, h4 + hr, h5 + hr, h6 + hr
  // Size
  margin: .5em 0 1.25em 0

p
  // Display
  display: block
  // Size
  padding: 0 0 0 0
  margin: 1.25em 0

span
  // Display
  display: inline

em, i
  // Aesthetic
  font-style: italic

strong, b
  // Aesthetic
  font-weight: bold

del, s
  // Aesthetic
  text-decoration: line-through

ins, u
  // Aesthetic
  text-decoration: underline

cite
  // Aesthetic
  font-style: italic

q
  // Display
  display: inline
  &::before
    // Content
    content: open-quote
  &::after
    // Content
    content: close-quote

/* Title: Anchors.
 * Description: Changes the display of anchor tags. */

// [Tag Names]
a, .anchor
  // Display
  display: inline
  // Aesthetic
  cursor: pointer
  color: #4553FB

a:hover, .anchor:hover
  // Aesthetic
  color: #4553FB

/* Title: Blockquote.
 * Description: Wrap large quotes around some text. */

// [Tag Names]
blockquote
  // Display
  display: block
  // Content
  position: relative
  // Size
  padding: 0 0 0 0
  margin: 1em 40px

blockquotes
  &::before
    // Display
    position: absolute
    top: -6px
    left: -25px
    // Content
    content: open-quote
    font-size: 3em
  &::after
    // Display
    position: absolute
    top: 10px
    right: -25px
    // Content
    content: close-quote
    font-size: 3em

/* Title: User selection. 
 * Description: Modifies the highlight color of the website.
 * Note: .section is intended to be used with inline elements only.
 * Note: Compatibility for IE9 and higher. */

// [Tag Names]
*
  &::selection, &::-moz-selection
    // Aesthetic
    color: #FFFFFF
    background-color: #4553FB

.selection
  // Aesthetic
  color: #FFFFFF
  background-color: #4553FB

/* Title: Scrollbar Properties
 * Description: Changes the look and feel of the scrollbar.
 * Note: As of October 16, 2018, this does not work with firefox or edge.
 * Note: https://css-tricks.com/custom-scrollbars-in-webkit/
 * Note: http://almaer.com/blog/creating-custom-scrollbars-with-css-how-css-isnt-great-for-every-task */


// [Tag Names]

::-webkit-scrollbar
  // Size
  width: 15px
  height: 15px

::-webkit-scrollbar-button

::-webkit-scrollbar-track
  // Aesthetic
  background-color: transparent
  box-shadow: inset 0 0 0 0 transparent
  border-radius: 20px

::-webkit-scrollbar-track-piece

::-webkit-scrollbar-thumb
  // Size
  border-width: 3px
  // Aesthetic
  background-color: #4553FB
  background-clip: padding-box
  border-style: solid
  border-color: rgba(0, 0, 0, 0)
  border-radius: 20px
  &:hover
    // Aesthetic
    background-color: #4553FB

::-webkit-scrollbar-corner, ::-webkit-resizer

/* Title: Form Elements 
 * Description: Color and display properties for input boxes and textareas. */

// [Tag Names]

form
  // Display
  display: block

fieldset
  // Display
  display: block
  // Size
  padding: 1em 2.5%
  margin: 0 0
  border-width: 1px
  border-style: solid
  // Aesthetic
  border-color: #000000

legend
  // Size
  padding: 0 0
  margin: 0 0

label
  // Display
  display: block
  // Size
  margin: 0
  // Content
  font-size: inherit
  line-height: inherit
  // Aesthetic
  font-weight: bold

input, select, textarea
  // Display
  display: block
  // Size
  padding: 0 0
  margin: 0 0
  border: 1px
  // Aesthetic
  background-color: transparent
  border-style: solid
  border-color: inherit

input
  &[type="radio"], &[type="checkbox"]
    // Display
    display: inline-block
    // Size
    width: initial

::-webkit-input-placeholder
  // Chrome/Opera/Safari
  // Insert Placeholder CSS Information Here.

::-moz-placeholder
  // Firefox 19+
  // Insert Placeholder CSS Information Here.

\:-ms-input-placeholder
  // IE 10+
  // Insert Placeholder CSS Information Here.

\:-moz-placeholder
  // Firefox 18-
  // Insert Placeholder CSS Information Here.

input
  &[type="submit"], &[type="reset"]
    // Display
    display: block
    // Size
    padding: 2px 5px
    margin: 0 0
    border: 0
    // Aesthetic
    cursor: pointer
    color: inherit
    background-color: transparent
    border-style: solid
    border-color: transparent

button
  // Display
  display: block
  // Size
  padding: 2px 5px
  margin: 0 0
  border: 0
  // Aesthetic
  cursor: pointer
  color: inherit
  background-color: transparent
  border-style: solid
  border-color: transparent

input
  &[type="submit"]:hover, &[type="reset"]:hover
    // Aesthetic
    color: inherit
    background-color: transparent

button:hover
  // Aesthetic
  color: inherit
  background-color: transparent

// 
 * Title: UnOrdered List Elements.
 * Description: Controls the unordered list html tag <ul>.

// [Tag Names]

ul
  // Display
  display: block
  // Size
  padding: 0 0 0 40px
  margin: 1em 0
  // Aesthetic
  list-style-type: disc

li
  // Display
  display: list-item

// Title: Ordered List Elements.
 * Description: Controls the ordered list html tag ol>.

// [Tag Names]

ol
  // Display
  display: block
  // Size
  padding: 0 0 0 40px
  margin: 1em 0
  // Aesthetic
  list-style-type: decimal

li
  // Display
  display: list-item

// Title: Table Elements.
 * Description: Controls the render structure of table elements.

// [Tag Names]

table
  // Display
  display: table
  // Size
  border-width: 1px
  // Content
  text-align: left
  // Aesthetic
  border-color: #000000
  border-style: solid

caption
  // Display
  display: table-caption
  // Size
  border-width: inherit
  // Content
  text-align: center
  // Aesthetic
  border-color: inherit
  border-style: inherit

thead
  // Display
  display: table-header-group
  // Size
  padding: 0
  margin: 0
  border-width: inherit
  // Content
  text-align: inherit
  vertical-align: middle
  // Aesthetic
  border-color: inherit
  border-style: inherit

tbody
  // Display
  display: table-row-group
  // Size
  padding: 0
  margin: 0
  border-width: inherit
  // Content
  text-align: inherit
  vertical-align: middle
  // Aesthetic
  border-color: inherit
  border-style: inherit

tfoot
  // Display
  display: table-footer-group
  // Size
  padding: 0
  margin: 0
  border-width: inherit
  // Content
  text-align: inherit
  vertical-align: middle
  // Aesthetic
  border-color: inherit
  border-style: inherit

tr
  // Display
  display: table-row
  // Size
  padding: 0
  margin: 0
  border-width: inherit
  // Aesthetic
  border-color: inherit
  border-style: inherit

th
  // Display
  display: table-cell
  // Size
  padding: 0
  margin: 0
  border-width: inherit
  // Content
  text-align: inherit
  // Aesthetic
  font-weight: bold
  border-color: inherit
  border-style: inherit

td
  // Display
  display: table-cell
  // Size
  padding: 0
  margin: 0
  border-width: inherit
  // Content
  text-align: inherit
  // Aesthetic
  border-color: inherit
  border-style: inherit

colgroup
  // Display
  display: table-column-group

col
  // Display
  display: table-column